import React from 'react';
import { makeStyles } from '@material-ui/core/styles';

import { title, myFontSize } from "myAssets/jss/my-react-kit.js";
import GridContainer from 'components/Grid/GridContainer';
import GridItem from 'components/Grid/GridItem';

import system1 from "assets/img/system1.jpg";
import system2 from "assets/img/system2.jpg";
import system3 from "assets/img/system3.jpg";


const styles = {
    section: {
        padding: "100px 0",
        textAlign: "center"
      },
      title: {
        ...title,
        ...myFontSize.lg,
        color : "black",
        letterSpacing : "-0.2rem",
        marginBottom: "1rem",
        marginTop: "0",
        minHeight: "32px",
        textDecoration: "none"
      },
      description1: {
        color: "#bdc3c7",
        ...myFontSize.xs,
        marginBottom: "0",
        padding : "0 2rem"
      },
      description2: {
        color : "#747d8c",
        ...myFontSize.sm,
        marginTop : "0",
        marginBottom: "0",
        fontWeight : "400",
        padding : "0 2rem"
      },
      verticalAlign: {
        display : "flex",
        alignItems : "center",
        flexDirection : "column",
        justifyContent : "center",
        width : "100%",
        height : "100%"
      },
      marginBottom: {
          marginBottom : "3rem"
      }
}

const useStyle = makeStyles(styles);

const SystemSection = () => {
    const classes = useStyle();
    return (
        <div id="systemSection" className={classes.section}>
            <GridContainer justify="center" className={classes.marginBottom}>
                <GridItem xs={12} sm={12} md={8}>
                    <h5 className={classes.description1}>BARENJUNGPLANT DENTAL CLINIC</h5>
                    <h2 className={classes.title}>바른정플란트치과 감염시스템</h2>
                    <h5 className={classes.description2}>
                            2차 감염 방지를 위해 고압 증기 별균 소독과
                            <br />
                            대학병원급 감염관리 시스템을 갖추고 있습니다.
                    </h5>
                </GridItem>
            </GridContainer>
            <GridContainer justify="center" spacing={8}>
                <GridItem xs={12} sm={12} md={6} >
                 <img src={system1} alt="First slide" className="slick-image" style={{padding : '5px', width : "100%"}} />
                </GridItem>
                <GridItem xs={12} sm={12} md={6} >
                    <div className={classes.verticalAlign}>
                        <h3 className={classes.title}>마셔도 됩니다!</h3>
                        <h5 className={classes.description2}>
                                치료 진료용수 관리를 위해 미산서차아염소산수를 모든 체어와 기구에 공급하여 안심하고 치과진료를 받으실 수 있습니다. 
                                <br />
                                주기적 필터 관리는 물론 내부 수관 역시 살균소독하여 청결하게 유지하고 있습니다.
                        </h5>
                    </div>
                </GridItem>
                <GridItem xs={12} sm={12} md={6} >
                    <div className={classes.verticalAlign}>
                        <h3 className={classes.title}>철저한 감염관리 시스템</h3>
                        <h5 className={classes.description2}>
                                모든 진료 기구는 고압증기멸균기, 초음파소독기, 자외선 소독기, 핸드피스 소독기, 살균 스프레이 등 멸균장비를 통해 항상 최상의 상태로 관리하고 있습니다.
                        </h5>
                    </div>
                </GridItem>
                <GridItem xs={12} sm={12} md={6} >
                 <img src={system2} alt="First slide" className="slick-image" style={{padding : '5px', width : "100%"}} />
                </GridItem>
                <GridItem xs={12} sm={12} md={6} >
                 <img src={system3} alt="First slide" className="slick-image" style={{padding : '5px', width : "100%"}} />
                </GridItem>
                <GridItem xs={12} sm={12} md={6} >
                    <div className={classes.verticalAlign}>
                        <h3 className={classes.title}>6단계 소독 과정 메뉴얼</h3>
                        <h5 className={classes.description2}>
                                전직원의 멸균/소독에 대한 교육을 통해 철저한 관리를 하고 있으며, 메뉴얼에 따른 6단계 소독과정으로 소독하고 있습니다.
                        </h5>
                    </div>
                </GridItem>


            </GridContainer>
        </div>
    );
}


export default SystemSection;