import React from 'react';
import Helmet from 'react-helmet';


const HelmetSection = ({brand, ogImg}) => {
    return (
        <Helmet>
            <title>{brand}</title>
            <meta name="keywords" content="화명동치과, 임플란트, 네비게이션임플란트, 바른정플란트치과, 부산치과, 사랑니발치, 야간진료, 충치치료, 크라운보철, 수면진정치료, 턱관절질환" />
            <meta name="subject" content="화명동 바른정플란트치과" />
            <meta name='description' content="고난위 임플란트 수술은 바른정플란트치과에서..." />
            <meta name="author" content="바른정플란트치과" />
            <meta property="og:title" content="화명동 바른정플란트치과" />
            <meta property="og:description" content="고난위 임플란트 수술은 바른정플란트치과에서..." />
            <meta property="og:image" content={ogImg} />

        </Helmet>
    )
}

export default HelmetSection;
