import React from 'react';

import GridItem from 'components/Grid/GridItem';

export default class extends React.Component {



 async componentDidMount() {
     // The map, centered at Uluru
     const googles = await window.google;
     const googleMap = googles && googles.maps;
     // The location of Uluru
     const uluru = {lat:35.226795, lng: 129.010621};
       
       const initMap = () => {

        const map = googleMap && (new googleMap.Map(
            document.getElementById('map'), {zoom: 15, center: uluru}));
        // The marker, positioned at Uluru
        const marker = googleMap && (new googleMap.Marker({position: uluru, map: map, title: '바른정플란트치과'}));
    }
     initMap();
}
    
    render() {
        return (
            <GridItem xs={12} sm={12} md={6}>
                <div style={{width : "100%", height : "100%"}}>
                    <div style={{padding : "10px", height : "100%"}}>
                        <div id="map" style={{height : "100%", width : "100%", minHeight: "300px"}}></div>
                    </div>
                </div>
            </GridItem>
        )
    }
}
