import React from 'react';
import { makeStyles } from '@material-ui/core/styles';

import { title, myFontSize } from "myAssets/jss/my-react-kit.js";

import GridContainer from 'components/Grid/GridContainer';
import GridItem from 'components/Grid/GridItem';

import locationSection from "assets/img/locationSection.jpg";
import GoogleMapSection from './GoogleMapSection';


const styles = {
    section: {
        padding: "100px 0",
        textAlign: "center",
        backgroundColor : "#574134"
      },
      title: {
        ...title,
        ...myFontSize.lg,
        color : "#fab541",
        letterSpacing : "-0.2rem",
        marginBottom: "1rem",
        marginTop: "0",
        minHeight: "32px",
        textDecoration: "none",
        transform: "scale(1, 1.1)"
      },
      description1: {
        color: "#bdc3c7",
        ...myFontSize.xs,
        marginBottom: "0",
        transform: "scale(1, 1.1)",
        padding : "0 2rem"
      },
      description2: {
        color : "#bdc3c7",
        ...myFontSize.sm,
        marginTop : "0",
        marginBottom: "10px",
        fontWeight : "400",
        padding : "0 2rem"
      }
}

const useStyle = makeStyles(styles);


const LocationSection = () => {
    const classes = useStyle();
    return (
        <div id="locationSection" className={classes.section}>
            <GridContainer justify="center">
                <GridItem xs={12} sm={12} md={8}>
                        <h5 className={classes.description1}>BARENJUNGPLANT DENTAL CLINIC</h5>
                        <h2 className={classes.title}>바른정플란트치과 오시는 길</h2>
                        <h5 className={classes.description2}>
                                부산 2호선 수정역 3번 출구에서 도보로 5분
                        </h5>
                </GridItem>
                <GridItem xs={12} sm={12} md={6}>
                    <img src={locationSection} alt="First slide" className="slick-image" style={{padding : "10px", width : "100%"}} />
                </GridItem>
                <GoogleMapSection />
            </GridContainer>
        </div>
    );
}

export default LocationSection;